import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'transformLoop'
})
export class TransformLoopPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    let outputArr = [];
    for (let i = 0; i < value; i++) {
      outputArr.push(i);
    }
    return outputArr;
  }

}



