import {ICustomer} from '../customer';
import {IRoomType} from '../hotel/room';
import {IHotel} from '../hotel/hotel';

export interface IOrderDetail {
  orderId?: string;
  hotelName?: string;
  hotelAddress?: string;
  customer?: ICustomer;
  room?: IRoomType;
  quantity?: number;
  dateIn?: string;
  dateOut?: string;
  status?: EnumOrderStatus;
  staffId?: string;
  total?: number;
  note?: any;
}

export enum EnumOrderStatus {
  Waiting,
  Confirmed,
  Canceled
}



